.top-actions {
  --icon-width: 18px;

  display: grid;
  grid-template-columns: 1fr var(--icon-width) var(--icon-width);
  align-items: center;
  gap: 30px;

  @include onDesktop() {
    --icon-width: 24px;
    gap: 40px;
  }
}

.top-actions--two-colums {
  grid-template-columns: 1fr var(--icon-width);
}
